import PropTypes from "prop-types"
import React, { memo, useEffect, useState } from "react"
import { CSSTransition, SwitchTransition } from "react-transition-group"

import IconClose from "@/assets/img/svg/icon-close"
import { BrowserWrapper } from "./style"
import IconArrowLeft from "@/assets/img/svg/icon-arrow-left"
import IconArrowRight from "@/assets/img/svg/icon-arrow-right"

const PictureBrowser = memo((props) => {
  const { picture_urls, closeFn } = props
  // 显示时滚动功能消失
  useEffect(() => {
    document.body.style.overflow = "hidden"

    return () => {
      document.body.style.overflow = "auto"
    }
  }, [])

  const [currentIndex, setCurrentIndex] = useState(0)
  const [isNext, setIsNext] = useState(true)

  const handleBtnClick = (isNext = true) => {
    if (!isNext) {
      setCurrentIndex(currentIndex ? currentIndex - 1 : picture_urls.length - 1)
    } else {
      setCurrentIndex(
        currentIndex === picture_urls.length - 1 ? 0 : currentIndex + 1
      )
    }
    setIsNext(isNext)
  }
  return (
    <BrowserWrapper isNext={isNext}>
      <div className="top">
        <div className="closeBtn" onClick={closeFn}>
          <IconClose />
        </div>
      </div>
      <div className="slider">
        <div className="control">
          <div className="btn left" onClick={(e) => handleBtnClick(false)}>
            <IconArrowLeft size={77} />
          </div>
          <div className="btn right" onClick={(e) => handleBtnClick()}>
            <IconArrowRight size={77} />
          </div>
        </div>
        <div className="picture">
          <SwitchTransition mode="in-out">
            <CSSTransition
              key={picture_urls[currentIndex]}
              timeout={150}
              classNames="pic"
            >
              <img src={picture_urls[currentIndex]} alt="" />
            </CSSTransition>
          </SwitchTransition>
        </div>
      </div>
      <div className="preview">
        <span className="desc">
          {currentIndex + 1}/{picture_urls.length}
        </span>
      </div>
    </BrowserWrapper>
  )
})

PictureBrowser.propTypes = {
  picture_urls: PropTypes.array,
  closeFn: PropTypes.func
}

export default PictureBrowser
