import styled from "styled-components"

export const BrowserWrapper = styled.div`
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #333;

  .top {
    position: relative;
    height: 90px;

    .closeBtn {
      position: absolute;
      top: 30px;
      right: 60px;
      cursor: pointer;
      z-index: 99;
    }
  }

  .slider {
    display: flex;
    flex: 1;
    justify-content: center;
    min-height: 100px;

    .control {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      display: flex;
      justify-content: space-between;
      color: #fff;

      .btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 83px;
        height: 100%;
        cursor: pointer;
      }
    }

    .picture {
      position: relative;
      height: 100%;
      overflow: hidden;
      width: 100%;
      max-width: 105vh;

      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        margin: 0 auto;
        height: 100%;
        user-select: none;
      }

      /* animation */
      .pic-enter {
        transform: translateX(
          ${(props) => (props.isNext ? "100%" : "-100%")}
        ); //判断滑动方向
        opacity: 0;
      }
      .pic-enter-active {
        transform: translateX(0);
        opacity: 1;
        transition: all ease 150ms;
      }

      .pic-exit {
        opacity: 1;
      }
      .pic-exit-active {
        opacity: 0;
        transition: all ease 150ms;
      }
    }
  }

  .preview {
    margin-top: 10px;
    height: 100px;
    display: flex;
    justify-content: center;

    .desc {
      margin-top: 30px;
      color: #fff;
      font-size: 20px;
    }
  }
`
