import React, { memo, useState } from "react"
import { shallowEqual, useSelector } from "react-redux"
import PictureBrowser from "@/base-ui/picture-browser"
import { PictureWrapper } from "./style"

const DetailPictures = memo(() => {
  const { item } = useSelector(
    (state) => ({
      item: state.detail.item
    }),
    shallowEqual
  )

  const [isShowBrowser, setIsShowBrowser] = useState(false)
  return (
    <PictureWrapper>
      <div className="pictures" onClick={(e) => setIsShowBrowser(true)}>
        <div className="left">
          <div className="item">
            <img src={item.picture_urls?.[0]} alt="" />
            <div className="cover"></div>
          </div>
        </div>
        <div className="right">
          {item.picture_urls.slice(1, 5).map((i) => (
            <div className="item" key={i}>
              <img src={i} alt="" />
              <div className="cover"></div>
            </div>
          ))}
        </div>
      </div>

      {isShowBrowser && (
        <PictureBrowser
          picture_urls={item.picture_urls}
          closeFn={(e) => setIsShowBrowser(false)}
        />
      )}
    </PictureWrapper>
  )
})

export default DetailPictures
